<template src="./index.html"></template>

<script>
// @ is an alias to /src
import "./style.scss";
import {
    Get_Yahoo_Price,
    Save_Price_to_DB,
    Save_Trading_Date_to_DB,
    Save_PER_to_DB,
    Save_Corporation_to_DB,
    Check_Stock_With_Date,
    Save_MACD_to_DB,
    Save_MA_to_DB,
    Save_KD_to_DB,
    Save_RSI_to_DB,
} from "../../api/api.js";
export default {
    name: "UpdateButton",
    props: [],
    mounted: function () {},
    data() {
        return {
            disabled: false,
            price: [],
        };
    },
    components: {},
    computed: {},
    methods: {
        isJSON(str) {
            try {
                return JSON.parse(str) && !!str;
            } catch (e) {
                return false;
            }
        },
        // 
        openInfo(title, message) {
            this.$notify.info({
                title: title,
                message: message,
                position: "top-left",
            });
        },
        openSuccess(title, message) {
            this.$notify({
                title: title,
                message: message,
                position: "top-left",
                type: "success",
            });
        },
        async handleClick() {
            try {
                await this.getTradingDate();
                await this.getPERData();
                await this.getCorporationsData();
                await this.getStockPrice();
                await this.openInfo("", "");
                this.disabled = false;
            } catch (error) {
                this.$notify.error({
                    title: "",
                    message: error,
                    position: "top-left",
                });
                this.disabled = false;
            }
        },
        // 1.
        async getTradingDate() {
            this.disabled = true;
            let response = await Save_Trading_Date_to_DB();
            this.$store.commit("setLastDate", response.data.date[response.data.date.length-1]);
            this.openSuccess("", "");
        },
        // 2.
        async getPERData() {
            await Save_PER_to_DB();
            this.openSuccess("", "");
        },
        // 3.
        async getCorporationsData() {
            await Save_Corporation_to_DB();
            this.openSuccess("", "");
        },
        // 4.
        async getStockPrice() {
            try {
                let price = {};
                let AllPrice = {};
                let codes = this.$store.state.codes;
                codes = Object.keys(codes);
                let date = this.$store.state.lastDate;
                let tsmc = await Check_Stock_With_Date(2330, date);
                let delta = await Check_Stock_With_Date(8150, date);
                // 
                if (tsmc.data === true && delta.data === true) {
                    return false;
                } else {
                    for (let i = 0; i < codes.length; i++) {
                        const element = codes[i];
                        let response = await Get_Yahoo_Price(element);
                        response = response.data
                            .replace("jQuery111306382856220483186_1591513211276(", "")
                            .replace(");", "");
                        if (this.isJSON(response)) {
                            response = JSON.parse(response).ta;
                            response = response.map(element => {
                                let obj = {
                                    hight_price: element.h, // 
                                    low_price: element.l, // 
                                    open_price: element.o, // 
                                    close_price: element.c, // 
                                    volume: element.v, // 
                                    date: element.t,
                                };
                                return obj;
                            });
                            price[element] = response;
                            AllPrice[element] = response;
                            // 
                            if (i === Math.round(codes.length / 4)) {
                                this.savePrice(price); // 
                                price = {};
                            } else if (i === Math.round((codes.length / 4) * 2)) {
                                this.savePrice(price); // 
                                price = {};
                            } else if (i === Math.round((codes.length / 4) * 3)) {
                                this.savePrice(price); // 
                                price = {};
                            } else if (i === codes.length - 1) {
                                await this.savePrice(price); // 
                                await this.saveMACD();
                                await this.saveKD();
                                await this.saveMA();
                                await this.saveRSI();
                            }
                        }
                    }
                }
                this.openSuccess("", "");
            } catch (error) {
                console.log(error);
            }
        },
        // 4.Database
        async savePrice(price) {
            let data = `data=${JSON.stringify(price)}`;
            await Save_Price_to_DB(data).then(response => console.log(response));
        },
        // 5.MACD
        async saveMACD() {
            await Save_MACD_to_DB();
            this.openSuccess("", "MACD");
        },
        // 6.KD
        async saveKD() {
            await Save_KD_to_DB();
            this.openSuccess("", "KD");
        },
        // 7.MA
        async saveMA() {
            await Save_MA_to_DB();
            this.openSuccess("", "MA");
        },
        // 7.RSI
        async saveRSI() {
            await Save_RSI_to_DB();
            this.openSuccess("", "RSI");
        },
    },
    watch: {},
};
</script>
