<template>
    <div id="cards" v-if="!loading">
        <el-table :data="Object.keys(stocks)" style="width: 100%">
            <el-table-column type="index" label="" width="50"></el-table-column>
            <el-table-column type="data" label="" width="130">
                <template slot-scope="scope">
                    <a :href="filterPchomeUrl(scope.row)" target="_blank">{{ scope.row }}</a>
                </template>
            </el-table-column>
            <el-table-column type="data" label="" width="130">
                <template slot-scope="scope">
                    <a :href="filterYahooUrl(scope.row)" target="_blank">{{ stocks[scope.row][stocks[scope.row].length-1]['name'] }}</a>
                </template>
            </el-table-column>
            <el-table-column type="data" label="" width="130">
                <template slot-scope="scope">
                    {{ stocks[scope.row][stocks[scope.row].length-1]['c'] }}
                </template>
            </el-table-column>
            <el-table-column type="data" label="" width="130">
                <template slot-scope="scope">
                    <p> {{ stocks[scope.row][stocks[scope.row].length-1]['v'] }}</p>
                    <p> {{ stocks[scope.row][stocks[scope.row].length-1]['avgVolume'] }}</p>
                </template>
            </el-table-column>
            <el-table-column type="data" label="" width="130">
                <template slot-scope="scope">
                    <p>{{ stocks[scope.row][stocks[scope.row].length-1]['goldSection'][''] }}</p>
                    <p>{{ stocks[scope.row][stocks[scope.row].length-1]['goldSection'][''] }}</p>
                    <p>{{ stocks[scope.row][stocks[scope.row].length-1]['goldSection'][''] }}</p>
                    <p>{{ stocks[scope.row][stocks[scope.row].length-1]['goldSection'][''] }}</p>
                    <p>{{ stocks[scope.row][stocks[scope.row].length-1]['goldSection'][''] }}</p>
                </template>
            </el-table-column>
            <el-table-column label="" width="100">
                <template slot-scope="scope">
                    <p>: {{ customsPrices(stocks[scope.row][stocks[scope.row].length-1], "UP") }}</p>
                    <p>: {{ customsPrices(stocks[scope.row][stocks[scope.row].length-1], "Mid") }}</p>
                    <p>: {{ customsPrices(stocks[scope.row][stocks[scope.row].length-1], "Down") }}</p>
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <p>
                        <a :href="filterMainTrendUrl(scope.row)" target="_blank"></a>
                    </p>
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <p>
                        <a :href="filterMajorInvestorsUrl(scope.row)" target="_blank"></a>
                    </p>
                </template>
            </el-table-column>
            <el-table-column type="data" label="">
                <template slot-scope="scope">
                    {{ stocks[scope.row][stocks[scope.row].length-1]['w14'] }}
                </template>
            </el-table-column>
        </el-table>
        
    </div>
</template>

<style lang="scss">
</style>

<script>
import { Get_Steps_Stock  } from "../../api/api.js";
export default {
    name: "Cards",
    mounted: function () {
        this.getData();
    },
    props:['handleLoading','loading'],
    components: {},
    data() {
        return {
            stocks:null,
        };
    },
    computed: {},
    methods: {
        async getData() {
            try {
                let response = await Get_Steps_Stock();
                this.stocks = response.data;
                console.log(Object.keys(response.data));
                this.handleLoading();
            } catch (error) {
                console.log(error);
            }
        },
        filterMainTrendUrl(code) {
            let str = `https://www.wantgoo.com/stock/${code}/major-investors/main-trend#main-trend`;
            return str;
        },
        filterMajorInvestorsUrl(code){
            let str = `https://www.wantgoo.com/stock/${code}/major-investors/concentration`;
            return str;
        },
        filterPchomeUrl(code) {
            let str = "https://pchome.megatime.com.tw/stock/sto0/ock1/sid" + code + ".html";
            return str;
        },
        filterYahooUrl(code) {
            let str = "https://tw.stock.yahoo.com/q/ta?s=" + code;
            return str;
        },
        customsPrices(data, type) {
            let response = null;
            switch (type) {
                case "UP":
                    response =
                        parseInt(data["l"]) +
                        (parseInt(data["h"]) - parseInt(data["l"])) * 1.382;
                    break;
                case "Mid":
                    response = (parseInt(data["h"]) + parseInt(data["l"])) * 0.5;
                    break;
                case "Down":
                    response =
                        parseInt(data["h"]) -
                        (parseInt(data["h"]) - parseInt(data["l"])) * 1.382;
                    break;
                default:
                    break;
            }
            return Math.round(response * 100) / 100;
        },
    },
};
</script>
